.form-registration {
    background-color: #fff;
    padding: 30px;
    border: 1px solid rgba(0, 0, 0, 0.12);
    box-shadow: 0 0 25px rgba(0, 0, 0, 0.12);
    border-radius: 4px;
    max-width: 380px;
    box-sizing: border-box;
    &__title {
        margin-bottom: 19px;
        margin-top: 8px;

        @extend %h1;
    }
    &__name {
        margin-bottom: 10px;
    }
    &__surname {
        margin-bottom: 10px;
    }
    &__gender {
        margin-bottom: 45px;
    }
    &__email {
        margin-bottom: 10px;
        margin-top: 40px;
    }
    &__offers {
        margin-bottom: 17px;
        margin-top: 10px;
        & .toggle__input {
            @media screen and (max-width: 330px) {
                vertical-align: top;
            }
        }
    }
    &__submit {
        margin-bottom: 29px;
    }
    &__registered {
        display: flex;
        justify-content: space-between;
        align-items: center;
    }
    &__text {
        @extend %font-body;
        color: #000000;
    }
}