.menu {
    &__list {
        display: flex;
    }
    &__item {
        margin-right: 20px;
        position: relative;
    }
    &__link {
        @extend %font-body;
        opacity: 0.5;
        cursor: pointer;
        &_active {
            @extend %h3;
            font-size: 14px;
            text-transform: none;
            opacity: 0.75;
        }
    }
    &__sub-list {
        position: absolute;
        left: 0;
        background-color: white;
        display: none;
        min-width: 100%;
        z-index: 1;
    }
    &__sub-link {
        @extend %font-body;
        opacity: 0.5;
    }
    &__sub-item {
        padding-top: 10px;
    }
}

.show {
    display: block;
}